import { IAction } from "../store/action";
import { IAppState } from "../store/app/state";

export const combineReducers = (reducers: { [key: string]: (state: any, action: IAction) => any }) =>
    (state: { [key: string]: any }, action: IAction) => Object.keys(reducers)
        .reduce((acc: IAppState, key: string) => ({
            ...acc, [key]: reducers[key](state[key], action),
        }), state);