import { IonSlides, IonContent, IonSlide, IonImg, IonIcon, IonRow, IonCol, IonButton, IonFab } from '@ionic/react';
import { useParams } from 'react-router';
import IntroFullScreen from '../../components/intro-full-screen/IntroFullScreen';
import IntroSlides from '../../components/intro-slides/IntroSlides';
import { isPlatform } from '@ionic/react';
import { useLocalStorage } from '../../hooks/use-local-storage';

const IntroPage: React.FC = () => {

    const { name } = useParams<{ name: string; }>();
    const isMobile = isPlatform('mobile') || isPlatform('mobileweb')
    const [introDisplayed, setIntroDisplayed] = useLocalStorage('introDisplayed', false);

    const skipHandler = () => {
        setIntroDisplayed(true);
    };

    return (
        <IonContent class="ion-padding">
            {
                isMobile ?
                    <IntroSlides skip={skipHandler} />
                    : <IntroFullScreen skip={skipHandler} />
            }
        </IonContent >
    );
};

export default IntroPage;
