import { IonButtons, IonContent, IonFab, IonFabButton, IonHeader, IonIcon, IonItem, IonLabel, IonList, IonMenuButton, IonPage, IonSkeletonText, IonText, IonTitle, IonToolbar } from '@ionic/react';
import { add } from 'ionicons/icons';
import { useContext, useEffect } from 'react';
import { ICampaign } from '../../model/campaigns';
import { campaignsService } from '../../services/campaigns';
import { AppContext } from '../../store';
import { getCampaignsSuccess } from '../../store/campaigns/actions';
// import './Page.css';

const CampaignsPage: React.FC = () => {

  const { state, dispatch } = useContext(AppContext);

  useEffect(() => {
    if (!state.campaigns) {
      return;
    }

    campaignsService('competitions/2021/teams')
      .then((campaigns: ICampaign[]) => {
        dispatch(getCampaignsSuccess(campaigns));
      })
  }, [state.campaigns]);

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>Campaigns</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Campaigns</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonList>
          <IonItem detail>
            <IonLabel className="ion-text-wrap">
              <IonText color="primary">
                <h3>H3 Primary Title</h3>
              </IonText>
              <p>Paragraph line 1</p>
              <IonText color="secondary">
                <p>Paragraph line 2 secondary</p>
              </IonText>
            </IonLabel>
          </IonItem>
          <IonItem>
            <IonLabel className="ion-text-wrap">
              <h3><IonSkeletonText animated style={{ width: '30%' }} /></h3>
              <p><IonSkeletonText animated style={{ width: '60%' }} /></p>
              <p><IonSkeletonText animated style={{ width: '90%' }} /></p>
            </IonLabel>
          </IonItem>
        </IonList>
        <IonFab vertical="bottom" horizontal="end" slot="fixed">
          <IonFabButton>
            <IonIcon icon={add} />
          </IonFabButton>
        </IonFab>
      </IonContent>
    </IonPage>
  );
};

export default CampaignsPage;
