import {
  IonAvatar,
  IonContent,
  IonIcon,
  IonImg,
  IonItem,
  IonLabel,
  IonList,
  IonMenu,
  IonMenuToggle,
  IonTitle,
  IonToolbar,
} from '@ionic/react';

import { useLocation } from 'react-router-dom';
import { logOutOutline, paperPlaneOutline, paperPlaneSharp, settingsOutline, settingsSharp, gridOutline, gridSharp, logOutSharp } from 'ionicons/icons';
import './Menu.css';
import { useAuth0 } from '@auth0/auth0-react';

interface AppPage {
  url: string;
  iosIcon: string;
  mdIcon: string;
  title: string;
}

const appPages: AppPage[] = [
  {
    title: 'Dashboard',
    url: '/dashboard',
    iosIcon: gridOutline,
    mdIcon: gridSharp
  },
  {
    title: 'Campaigns',
    url: '/campaigns',
    iosIcon: paperPlaneOutline,
    mdIcon: paperPlaneSharp
  },
];

const settingsPages: AppPage[] = [
  {
    title: 'Settings',
    url: '/settings',
    iosIcon: settingsOutline,
    mdIcon: settingsSharp
  }
];

const Menu: React.FC = () => {
  const location = useLocation();
  const { logout, isAuthenticated, user } = useAuth0();

  const otherList = isAuthenticated ?
    (
      <IonMenuToggle autoHide={false}>
        <IonItem lines="none" onClick={() => logout({ returnTo: window.location.href })}>
          <IonIcon slot="start" ios={logOutOutline} md={logOutSharp} />
          <IonLabel>Log Out</IonLabel>
        </IonItem>
      </IonMenuToggle>
    ) : null;

  return (
    <IonMenu contentId="main" type="overlay">
      <IonToolbar>
        <IonAvatar slot="start" class="ion-margin"><img src={user?.picture} alt={user?.name} /></IonAvatar>
        <IonTitle>Cirkl Admin</IonTitle>
        <IonTitle size="small">{user?.name}</IonTitle>
      </IonToolbar>

      <IonContent>
        <IonList id="main-list">
          {appPages.map((appPage, index) => {
            return (
              <IonMenuToggle key={index} autoHide={false}>
                <IonItem className={location.pathname === appPage.url ? 'selected' : ''} routerLink={appPage.url} routerDirection="none" lines="none" detail={false}>
                  <IonIcon slot="start" ios={appPage.iosIcon} md={appPage.mdIcon} />
                  <IonLabel>{appPage.title}</IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
          })}
        </IonList>

        <IonList id="settings-list">
          {settingsPages.map((appPage, index) => {
            return (
              <IonMenuToggle key={index} autoHide={false}>
                <IonItem className={location.pathname === appPage.url ? 'selected' : ''} routerLink={appPage.url} routerDirection="none" lines="none" detail={false}>
                  <IonIcon slot="start" ios={appPage.iosIcon} md={appPage.mdIcon} />
                  <IonLabel>{appPage.title}</IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
          })}
          {otherList}
        </IonList>
      </IonContent>
    </IonMenu>
  );
};

export default Menu;
